.post-content :is(article, aside, details,
figcaption, figure,
footer, header,
main, menu, nav,
section, summary) { display:block; }
.post-content :is(audio, canvas, progress, video) {
    display: inline-block;
    vertical-align: baseline;
}
.post-content audio:not([controls]) { display: none; height: 0; }
[hidden], template { display: none; }
.post-content a { background-color: transparent;}
.post-content a:active, a:hover { outline: 0; }
.post-content abbr[title] { border-bottom: 1px dotted; }
.post-content :is(b, strong) { font-weight: bold; }
.post-content dfn { font-style: italic; }
.post-content h1 { font-size: 2em; margin: 0.67em 0; }
.post-content mark { background: #ff0; color: #000; }
.post-content small { font-size: 80%; }
.post-content :is(sub, sup) {
    font-size: 75%;
    line-height: 0;
    position: relative;
    vertical-align: baseline;
}
.post-content sup { top: -0.5em; }
.post-content sub { bottom: -0.25em; }
.post-content img { border: 0; }
.post-content svg:not(:root) { overflow: hidden; }
.post-content figure { margin: 1em 40px; }
.post-content hr { box-sizing: content-box; height: 0; }
.post-content pre { overflow: auto; }
.post-content :is(code, kbd, pre, samp) { font-family: monospace, monospace; font-size: 1em; }
.post-content legend { border: 0; padding: 0; }
.post-content optgroup { font-weight: bold; }
.post-content table{ border-collapse: collapse; border-spacing: 0; }
.post-content :is(td, th){ padding: 0; }

.post-content {
    height: 100%;
    max-height: 100%;
    font-family: "Merriweather", serif;
    letter-spacing: 0.01rem;
    font-size: 1.8rem;
    line-height: 1.75em;
    color: #3A4145;
    -webkit-font-feature-settings: 'kern' 1;
    -moz-font-feature-settings: 'kern' 1;
    -o-font-feature-settings: 'kern' 1;
    text-rendering: geometricPrecision;
}

.post-content :is(h1, h2, h3, h4, h5, h6) {
    -webkit-font-feature-settings: 'dlig' 1, 'liga' 1, 'lnum' 1, 'kern' 1;
    -moz-font-feature-settings: 'dlig' 1, 'liga' 1, 'lnum' 1, 'kern' 1;
    -o-font-feature-settings: 'dlig' 1, 'liga' 1, 'lnum' 1, 'kern' 1;
    color: #2E2E2E;
    line-height: 1.15em;
    margin: 0 0 0.4em 0;
    font-family: "Open Sans", sans-serif;
    text-rendering: geometricPrecision;
}

.post-content h1 {
    font-size: 5rem;
    letter-spacing: -2px;
    text-indent: -3px;
}

.post-content h2 {
    font-size: 3.2rem;
    letter-spacing: -1px;
}

.post-content h3 {
    font-size: 3rem;
    letter-spacing: -0.6px;
}

.post-content h4 {
    font-size: 2.5rem;
}

.post-content h5 {
    font-size: 2rem;
}

.post-content h6 {
    font-size: 2rem;
}

.post-content a:hover {
    color: #111;
}

.post-content :is(p, ul, ol, dl) {
    -webkit-font-feature-settings: 'liga' 1, 'onum' 1, 'kern' 1;
    -moz-font-feature-settings: 'liga' 1, 'onum' 1, 'kern' 1;
    -o-font-feature-settings: 'liga' 1, 'onum' 1, 'kern' 1;
    margin: 0 0 1.75em 0;
    text-rendering: geometricPrecision;
}

.post-content :is(ol, ul) {
    padding-left: 3rem;
}

.post-content :is(ol ol, ul ul, ul ol, ol ul) {
    margin: 0 0 0.4em 0;
    padding-left: 2em;
}

.post-content dl dt {
    float: left;
    width: 180px;
    overflow: hidden;
    clear: left;
    text-align: right;
    text-overflow: ellipsis;
    white-space: nowrap;
    font-weight: 700;
    margin-bottom: 1em;
}

.post-content dl dd {
    margin-left: 200px;
    margin-bottom: 1em
}

.post-content li {
    margin: 0.4em 0;
}

.post-content nav li {
    margin: .5em 0;
    padding-left: .5em;
    line-height: 1.6em;
}

.post-content nav ul {
    padding-right: 1.5em;
}

.post-content li li {
    margin: 0;
}

.post-content li > p:last-of-type {
    margin-bottom: 0;
}

.post-content hr {
    display: block;
    height: 1px;
    border: 0;
    border-top: #EFEFEF 1px solid;
    margin: 3.2em 0;
    padding: 0;
}

.post-contentv blockquote p {
    margin: 0.8em 0;
    font-style: italic;
}

.post-content blockquote small {
    display: inline-block;
    margin: 0.8em 0 0.8em 1.5em;
    font-size: 0.9em;
    color: #CCC;
}

.post-content blockquote small:before { content: "\2014 \00A0"; }

.post-content blockquote cite {
    font-weight: 700;
}

.post-content blockquote cite a { font-weight: normal; }

.post-content mark {
    background-color: #fdffb6;
}

.post-content :is(code, tt) {
    padding: 1px 3px;
    font-family: Inconsolata, monospace, sans-serif;
    font-size: 0.85em;
    white-space: pre-wrap;
    border: #E3EDF3 1px solid;
    background: #F7FAFB;
    border-radius: 2px;
    -webkit-font-feature-settings: "liga" 0;
    -moz-font-feature-settings: "liga" 0;
    font-feature-settings: "liga" 0;
}

.post-content pre {
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    margin: 0 0 1.75em 0;
    border: #E3EDF3 1px solid;
    width: 100%;
    padding: 10px;
    font-family: Inconsolata, monospace, sans-serif;
    font-size: 0.9em;
    white-space: pre;
    overflow: auto;
    background: #F7FAFB;
    border-radius: 3px;
}

.post-content :is(pre code, pre tt) {
    font-size: inherit;
    white-space: pre-wrap;
    background: transparent;
    border: none;
    padding: 0;
}

.post-content kbd {
    display: inline-block;
    margin-bottom: 0.4em;
    padding: 1px 8px;
    border: #CCC 1px solid;
    color: #666;
    text-shadow: #FFF 0 1px 0;
    font-size: 0.9em;
    font-weight: 700;
    background: #F4F4F4;
    border-radius: 4px;
