        min-height: 180px;
        padding: 10% 0;
    }

    .blog-logo img {
        padding: 4px 0;
    }

    .page-title {
        font-size: 4rem;
        letter-spacing: -1px;
    }

    .page-description {
        font-size: 1.8rem;
        line-height: 1.5em;
    }

    .post {
        font-size: 0.95em
    }

    body:not(.post-template) .post-title {
        font-size: 3.2rem;
    }

    body.page-template .post-title {
        font-size: 4.5rem;
    }

    .post-content hr {
        margin: 2.4em 0;
    }

    .post-content :is(ol, ul) {
        padding-left: 2em;
    }

    .post-content h1 {
        font-size: 4.5rem;
        text-indent: -2px;
    }

    .post-content h2 {
        font-size: 3.6rem;
    }

    .post-content h3 {
        font-size: 3.1rem;
    }

    .post-content h4 {
        font-size: 2.5rem;
    }

    .post-content h5 {
        font-size: 2.2rem;
    }

    .post-content h6 {
        font-size: 1.8rem;
    }

    .author-profile {
        padding-bottom: 4rem;
    }

    .author-profile .author-bio {
        font-size: 1.6rem;
    }

    .author-meta span {
        display: block;
        margin: 1.5rem 0;
    }
    .author-profile .author-meta span {
        font-size: 1.6rem;
    }

    .post-head.main-header {
        height:45vh;
    }

    .tag-head.main-header,
    .author-head.main-header {
        height: 30vh;
    }

    .no-cover.post-head.main-header {
        height: 55px;
        padding: 0;
    }

    .no-cover.author-head.main-header {
        padding: 0;
    }

    .gh-subscribe {
        padding: 2rem;
    }

    .gh-subscribe-title {
        font-size: 2rem
    }

    .gh-subscribe p {
        font-size: 1.4rem;
    }

    .read-next {
        -webkit-box-orient: vertical;
        -webkit-box-direction: normal;
        -webkit-flex-direction: column;
        -ms-flex-direction: column;
        flex-direction: column;
        margin-top: 4rem;
    }

    .read-next p {
        display: none;
    }

    .read-next-story.no-cover + .read-next-story.no-cover {
        border-top: rgba(0,0,100,0.06) 1px solid;
        border-left: none;
    }

}

@media screen and (max-width: 992px) {
  .main-navigation {
    display: none !important; }

  .main-navigation-scroll {
    display: flex !important; }

  .author-blog h4{
    margin:0 0 0 50px;
  }
  .author-date{
    margin:-16px 0 0 50px;
  } 
}

/* ==========================================================================
   Media Queries - Smaller than 500px
   ========================================================================== */

@media only screen and (max-width: 500px) {

    .author-image{
        margin-left: -20px;   
    }

    .post-header{
        margin-top: 80px;
    }

    .main-header {
        margin-bottom: 15px;
        height: 40vh;
    }

    .no-cover.main-header {
        height: 30vh;
    }

    .paged .main-header {
        max-height: 20vh;
        min-height: 160px;
        padding: 10% 0;
    }

    .main-nav {
        padding: 5px;
        margin-bottom: 2rem;
    }

    .blog-logo {
        padding: 5px;
    }

    .blog-logo img {
        height: 30px;
    }

    .menu-button {
        padding: 0 5px;
        border-radius: 0;
        border-width: 0;
        color: #2e2e2e;
        background: transparent;
    }

    .main-nav.overlay a:hover {
        color: #fff;
        border-color: transparent;
        background: transparent;
    }

    .no-cover .main-nav.overlay {
        background: none;
    }
    .no-cover .main-nav.overlay .menu-button {
        border: none;
    }

    .main-nav.overlay .menu-button {
        border-color: transparent;
    }

    .nav-title {
        top: 25px;

    }

    .nav-close {
        position: absolute;
        top: 18px;
    }

    .inner,
    .pagination {
        width: auto;
        margin: 2rem auto;
    }

    .post {
        width: auto;
        margin-top: 2rem;
        margin-bottom: 2rem;
        margin-left: 16px;
        margin-right: 16px;
        padding-bottom: 2rem;
        line-height: 1.65em;
    }

    .post-date {
        display: none;
    }

    .post-template .post-header,
    .page-template .post-header {
        margin-bottom: 2rem;
    }

    .post-template .post-date,
    .page-template .post-date {
        display: inline-block;
    }

    .post-content hr {
        margin: 1.75em 0;
    }

    .post-content :is(p, ul, ol, dl) {
        font-size: 0.95em;
        margin: 0 0 2.5rem 0;
    }

    .page-title {
        font-size: 3rem;
    }

    .post-excerpt p {
        font-size: 0.85em;
    }

    .page-description {
        font-size: 1.6rem;
    }

    .post-content :is(h1, h2, h3, h4, h5, h6) {
        margin: 0 0 0.3em 0;
    }

    .post-content h1 {
        font-size: 2.8rem;
        letter-spacing: -1px;
    }

    .post-content h2 {
        font-size: 2.4rem;
        letter-spacing: 0;
    }

    .post-content h3 {
        font-size: 2.1rem;
    }

    .post-content h4 {
        font-size: 1.9rem;
    }

    .post-content h5 {
        font-size: 1.8rem;
    }

    .post-content h6 {
        font-size: 1.8rem;
    }

    body:not(.post-template) .post-title {
        font-size: 2.5rem;
    }

    body.page-template .post-title {
        font-size: 2.8rem;
    }

    .post-template .site-footer,
    .page-template .site-footer {
        margin-top: 0;
    }

    .post-content img {
        padding: 0;
        width: calc(100% + 32px); /* expand with to image + margins */
        min-width: 0;
        max-width: 112%; /* fallback when calc doesn't work */
    }

    .post-meta {
        font-size: 1.3rem;
        margin-top: 3rem;
    }

    .post-footer {
        padding: 5rem 0 3rem 0;
    }

    .post-footer .author {
        margin: 0 0 2rem 0;
        padding: 0 0 1.6rem 0;
        border-bottom: #EBF2F6 1px dashed;
    }

    .post-footer .share {
        position: static;
        width: auto;
    }

    .post-footer .share a {
        margin: 1.4rem 0.8rem 0 0.8rem;
    }

    .author-meta li {
        float: none;
        margin: 0;
        line-height: 1.75em;
    }

    .author-meta li:before {
        display: none;
    }

    .older-posts,
    .newer-posts {
        position: static;
        margin: 10px 0;
    }

    .page-number {
        display: block;
    }

    .site-footer {
        margin-top: 3rem;
    }

    .author-profile {
        padding-bottom: 2rem;
    }

    .post-head.main-header {
        height: 30vh;
    }

    .tag-head.main-header,
    .author-head.main-header {
        height: 20vh;
    }

    .post-footer .author-image {
        top: -60px;
    }

    .author-profile .author-image {
        margin-top: -70px;
    }

    .author-profile .author-meta span {
        font-size: 1.4rem;
    }

    .paged .main-header .page-description {
        display: none;
    }

    .gh-subscribe {
        padding: 15px;
    }

    .gh-subscribe form {
        margin-top: 10px;
    }

    .read-next {
        margin-top: 2rem;
        margin-bottom: -37px;
    }

    .read-next .post {
        width: 100%;
    }

}

